.navigator{
  display: none;
  position: fixed;
  bottom: 0;
  background: #fff;
  box-shadow: 0 1px 10px;
  z-index: 1;
}

.navigator nav ul{
  display: flex;
  align-items: center;
  text-align: center;
  list-style: none;
  padding: 0;
  margin: 0;
}

.navigator nav ul a{
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-decoration: none;
  color: #222327;
  padding: 1rem 0 0.5rem 0;
}

.navigator nav ul a.active{
  color: #fff !important;
}

.navigator nav ul a::before, .navigator nav ul a::after{
  content: '';
  position: absolute;
  bottom: 0;
  height: 100%;
  width: 27%;
  background: transparent;
  z-index: 99;
}

.navigator nav ul a.active::before{
  left: -10px;
  border-radius: 0 0 100% 0;
  background: #fff;
}

.navigator nav ul a.active::after{
  right: -10px;
  border-radius: 0 0 0 100%;
  background: #fff;
}

.indicator{
  position: absolute;
  width: 75%;
  height: 100%;
  background: #fff;
  border-top-right-radius: 50px;
  border-top-left-radius: 50px;
  z-index: -1;
}

.navigator nav ul a.active .indicator{
  background: #ff5e00;
}

.navigator nav ul a i{
  transform: translateY(5px);
}

.navigator nav ul a.active i{
  transform: translateY(0);
}

.navigator nav ul a span{
  font-size: .75rem;
  transform: translateY(35px);
  transition: .5s;
}

.navigator nav ul a.active span{
  transform: translateY(0px);
}

.navigator nav ul a.active .anim{
  content: '';
  position: absolute;
  background: #ff5e00;
  bottom: 0;
  width: 115%;
  height: 30px;
  z-index: -1;
}

@media screen and (max-width: 425px){
  .navigator{
    display: block;
  }
  .logo{
    display: none;
  }
  .logoSM{
    display: block !important;
  }
  .fa-bars.navbar-btn.navbar-toggler{
    display: none;
  }
}